// src/app/layout.tsx
import Navbar from "./components/Navbar"; // import your Navbar
import TopBar from "./components/TopBar";
import Footer from "./components/Footer";

export default function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <html lang="en">
      <body>
        <Footer />
        <TopBar />
        <Navbar />
        {children}
      </body>
    </html>
  );
}
